

import { mapStores } from 'pinia';
import Vue from 'vue';

import useSettingsStore from '@/store/SettingsStore';
import Utils, { PlayerUtils } from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

const QUALITY_H264 = [
    {text: '1080p (60fps) (4.50GB/h / 10.0Mbps)', value: '1080p-60fps'},
    {text: '1080p (4.50GB/h / 10.0Mbps)', value: '1080p'},
    {text: '810p (2.62GB/h / 5.8Mbps)', value: '810p'},
    {text: '720p (2.18GB/h / 4.9Mbps)', value: '720p'},
    {text: '540p (1.52GB/h / 3.4Mbps)', value: '540p'},
    {text: '480p (1.06GB/h / 2.3Mbps)', value: '480p'},
    {text: '360p (0.60GB/h / 1.3Mbps)', value: '360p'},
    {text: '240p (0.35GB/h / 0.8Mbps)', value: '240p'},
];

const QUALITY_H265 = [
    {text: '1080p (60fps) (1.80GB/h / 4.0Mbps)', value: '1080p-60fps'},
    {text: '1080p (1.37GB/h / 3.0Mbps)', value: '1080p'},
    {text: '810p (1.05GB/h / 2.3Mbps)', value: '810p'},
    {text: '720p (0.82GB/h / 1.8Mbps)', value: '720p'},
    {text: '540p (0.53GB/h / 1.2Mbps)', value: '540p'},
    {text: '480p (0.46GB/h / 1.0Mbps)', value: '480p'},
    {text: '360p (0.30GB/h / 0.7Mbps)', value: '360p'},
    {text: '240p (0.20GB/h / 0.4Mbps)', value: '240p'},
];

export default Vue.extend({
    name: 'Settings-General',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            Utils: Utils,
            PlayerUtils: PlayerUtils,

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            tv_streaming_quality: QUALITY_H264,

            // 
            panel_display_state: [
                {text: '', value: 'RestorePreviousState'},
                {text: '', value: 'AlwaysDisplay'},
                {text: '', value: 'AlwaysFold'},
            ],

            // 
            tv_panel_active_tab: [
                {text: '', value: 'Program'},
                {text: '', value: 'Channel'},
                {text: '', value: 'Comment'},
                {text: 'Twitter ', value: 'Twitter'},
            ],

            //  (KonomiTV-Settings.json) 
            import_settings_file: null as File | null,
        };
    },
    computed: {
        // SettingsStore  this.settingsStore 
        // ref: https://pinia.vuejs.org/cookbook/options-api.html
        ...mapStores(useSettingsStore),
    },
    created() {
        if (this.settingsStore.settings.tv_data_saver_mode === true) {
            this.tv_streaming_quality = QUALITY_H265;
        }
    },
    watch: {
        'settingsStore.settings.tv_data_saver_mode': {
            immediate: true,
            handler(val: boolean) {
                if (val === true) {
                    this.tv_streaming_quality = QUALITY_H265;
                } else {
                    this.tv_streaming_quality = QUALITY_H264;
                }
            },
        }
    },
    methods: {

        // 
        exportSettings() {

            //  JSON 
            const settings_json = JSON.stringify(this.settingsStore.settings, null, 4);

            //  Blob KonomiTV-Settings.json 
            const settings_json_blob = new Blob([settings_json], {type: 'application/json'});
            Utils.downloadBlobData(settings_json_blob, 'KonomiTV-Settings.json');
            this.$message.success('');
        },

        // 
        async importSettings() {

            // 
            if (this.import_settings_file === null) {
                this.$message.error('');
                return;
            }

            // 
            const result = await this.settingsStore.importClientSettings(this.import_settings_file);
            if (result === true) {
                this.$message.success('');
                window.setTimeout(() => this.$router.go(0), 300);
            } else {
                this.$message.error('');
            }
        },

        // 
        async resetSettings() {
            await this.settingsStore.resetClientSettings();
            this.$message.success('');
            window.setTimeout(() => this.$router.go(0), 300);
        },
    }
});

